<template>
  <Row class="h-60 label-sm border radius-4 transition" :class="cls" x="c" y="c">{{ label }}</Row>
</template>

<script setup>
const props = defineProps({
  label: {},
  selected: {},
})

const cls = $computed(() => {
  return props.selected ? 'border-c-primary c-primary' : 'border-c-d c-default bg-d'
})

</script>

