<template>
  <Page
    v-model:isRefresh="isRefresh"
    class="bg-white"
    :title="$t('ieo.subscriptionHistory')"
    hideHeader
    :leftArrow="true"
    :openRefresh="openRefresh"
    :onRefresh="onRefresh"
    :onClickRight="() => isSearch = !isSearch"
    :onClickLeft="onClickLeft"
  >
    <template #left>
      <van-icon class="title c-info" name="arrow-left" />
    </template>
    <template #right>
      <img v-if="!isSearch" class="w-32 h-32" src="@/image/ieo/search.webp">
      <img v-else class="w-32 h-32" src="@/image/ieo/search-active.webp">
    </template>

    <van-overlay :show="showStartTime" :z-index="101">
      <Column class="h-100b w-100b" y="e">
        <van-date-picker
          :modelValue="form.startTime"
          class="w-100b"
          type="datetime"
          :min-date="minDate"
          :title="$t('table.startTime')"
          @confirm="(v) => onTimeConfirm('startTime', v)"
          @cancel="onTimeCancel('startTime')"
        />
        <!-- <Column class="f-1 bg-error" /> -->
      </Column>
    </van-overlay>
    <van-overlay :show="showEndTime" :z-index="101">
      <Column class="h-100b w-100b" y="e">
        <van-date-picker
          :modelValue="form.endTime"
          class="w-100b"
          type="datetime"
          :min-date="minDate"
          :title="$t('table.endTime')"
          @confirm="(v) => onTimeConfirm('endTime', v)"
          @cancel="onTimeCancel('endTime')"
        />
        <!-- <Column class="f-1 bg-error" /> -->
      </Column>
    </van-overlay>

    <van-overlay :show="isSearch" :z-index="10" :lock-scroll="false" class="column">
      <Row class="p-t-92" />
      <Column class="SubscriptionHistory-search w-100b p-30 bg-white">
        <Column class="w-100b m-b-40">
          <Row class="label m-b-20 bold">{{ $t('ieo.projectName') }}</Row>
          <el-input v-model="form.name" class="w-100b" :placeholder="$t('form.inputPl', 'ieo.projectName')" />
        </Column>
        <Column class="w-100b m-b-20">
          <Row class="label m-b-20 bold">{{ $t('table.model') }}</Row>
          <Row class="w-100b f-wrap SelectBox" x="s">
            <SelectBox
              v-for="(item, i) in modelOptions"
              :key="item.value"
              class="w-31b m-b-20"
              :class="i ? '' : ''"
              :label="item.label"
              :selected="form.model == item.value"
              @click="() => form.model = item.value"
            />
          </Row>
        </Column>
        <Column class="w-100b m-b-20">
          <Row class="label m-b-20 bold">{{ $t('ieo.projectStatus') }}</Row>
          <Row class="w-100b f-wrap SelectBox" x="s">
            <SelectBox
              v-for="(item, i) in ieoStatusOptions"
              :key="item.value"
              class="w-31b m-b-20"
              :class="i ? '' : ''"
              :label="item.label"
              :selected="form.ieoStatus == item.value"
              @click="() => form.ieoStatus = item.value"
            />
          </Row>
        </Column>
        <Column class="w-100b m-b-30">
          <Row class="label m-b-20 bold">{{ $t('table.time') }}</Row>
          <Row class="w-100b label-sm">
            <Row class="h-60 f-1 f-center p-lr-20 border radius-4" x="sb" @click="() => showStartTime = true">
              <span>{{ form.startTime ? dayjs(form.startTime).format('YYYY/MM/DD HH:mm') : $t('table.startTime') }}</span>
              <img class="w-16 h-10" src="@/image/ieo/down-arrow.webp">
            </Row>
            <span class="c-info p-lr-14">/</span>
            <Row class="h-60 f-1 f-endTime p-lr-20 border radius-4" x="sb" @click="() => showEndTime = true">
              <span>{{ form.endTime ? dayjs(form.endTime).set('hour', 23).set('minute', 59).set('second', 59).format('YYYY/MM/DD HH:mm') :$t('table.endTime') }}</span>
              <img class="w-16 h-10" src="@/image/ieo/down-arrow.webp">
            </Row>
          </Row>
        </Column>
      </Column>
      <Row class="w-100b">
        <van-button class="f-1 h-80 c-info radius-0" type="default" @click="onReset"><span class="label">{{ $t('alert.reset') }}</span></van-button>
        <van-button class="f-1 h-80 radius-0" type="primary" @click="onSearch"><span class="label">{{ $t('alert.confirm') }}</span></van-button>
      </Row>
      <Column class="f-1" @click="() => isSearch = false" />
    </van-overlay>
    <Column v-loading="loading" class="w-100b f-1 bg-white" y="s" x="c">
      <van-list
        v-model:loading="loading"
        class="w-100b f-1"
        :finished="finished"
        :finished-text="$t('form.noMoreData')"
        :loading-text="$t('form.loading')"
        @refresh="onSearch"
        @load="onLoad"
      >
        <SubscriptionHistoryItem
          v-for="item in list"
          :key="item.id"
          :subscriptionTime="item.subscriptionTime"
          :ieoName="item.ieoName"
          :ieoModel="item.ieoModel"
          :price="item.price"
          :ieoStatus="item.ieoStatus"
          :totalCycle="item.totalCycle"
          :status="item.status"
          :modelOptions="modelOptions"
          :ieoStatusOptions="ieoStatusOptions"
          :statusOptions="statusOptions"
        />
      </van-list>
    </Column>
  </Page>
</template>
<script setup>
import { SubscriptionHistoryItem } from '@/view/ieo/components/index'

const route = useRoute()
const userStore = useUserStore()
const pages = reactive({
  ...PAGES,
})
const form = reactive({
  name: '',
  model: null,
  ieoStatus: null,
  startTime: undefined,
  endTime: undefined,
})
let list = $ref([])
let total = $ref()

let loading = $ref(true)
let isRefresh = $ref(false)
let openRefresh = $ref(true)

let showStartTime = $ref(false)
let showEndTime = $ref(false)
let isSearch = $ref(false)
const minDate = $ref(new Date(2022, 10, 1))
const finished = $computed(() => {
  if (loading == true) {
    return true
  } else if (list.length == total) {
    return true
  } else {
    return false
  }
})

const modelOptions = $computed(() => [
  { label: $t('table.all', 'table.model'), value: null },
  { label: $t('ieo.starterMode'), value: 0 },
  { label: $t('ieo.discountMode'), value: 1 },
])

const ieoStatusOptions = $computed(() => [
  { label: $t('table.status', 'table.model'), value: null },
  { label: $t('public.inProgress'), value: 0 }, // 进行中
  { label: $t('public.finished'), value: 1 }, // 已结束
  { label: $t('public.toBeIssued'), value: 2 }, // 代发放
  { label: $t('public.unlocking'), value: 3 }, // 解锁中
])

const statusOptions = $computed(() => [
  { label: $t('ieo.subscriptionStatusMap.0'), value: 0 },
  { label: $t('ieo.subscriptionStatusMap.1'), value: 1 },
  { label: $t('ieo.subscriptionStatusMap.2'), value: 2 },
])

const statusTabClass = {
  0: 'c-warning',
  1: 'c-success',
  2: 'c-primary',
  3: 'c-error',
  4: 'c-info',
}

const getData = async () => {
  try {
    loading = true
    if (isEmpty(userStore.token)) {
      return
    }
    const body = {
      ...form,
      ...pages,
    }
    if (form.startTime) {
      body.startTime = dayjs(form.startTime.join('-')).set('hour', 0).set('minute', 0).set('second', 0).valueOf()
    }
    if (form.endTime) {
      body.endTime = dayjs(form.endTime.join('-')).set('hour', 23).set('minute', 59).set('second', 59).valueOf()
    }
    const data = await ieoApi.subHistory(body)
    if (isArray(list)) {
      list.push(...data.records)
    } else {
      list = data.records
    }
    //     list[0].ieoStatus = 3
    // list[0].totalCycle = '0/1'
    total = data.total
  } finally {
    loading = false
  }
}
const onLoad = () => {
  pages.pageNum = pages.pageNum + 1
  getData()
}

const onReset = () => {
  form.name = ''
  form.model = null
  form.ieoStatus = null
  form.startTime = undefined
  form.endTime = undefined
  // getData()
}

const onSearch = () => {
  if (form.startTime && !form.endTime) {
    showNotify({ type: 'warning', message: $t('form.selectPl', 'table.endTime') })
    return
  }

  if (!form.startTime && form.endTime) {
    showNotify({ type: 'warning', message: $t('form.selectPl', 'table.startTime') })
    return
  }

  list = []
  pages.pageNum = 1
  getData()
  isSearch = false
}

const onRefresh = async () => {
  try {
    isRefresh = true
    pages.pageNum = 1
    const body = {
      ...form,
      ...pages,
    }
    if (form.startTime) {
      body.startTime = dayjs(form.startTime.join('-')).set('hour', 0).set('minute', 0).set('second', 0).valueOf()
    }
    if (form.endTime) {
      body.endTime = dayjs(form.endTime.join('-')).set('hour', 23).set('minute', 59).set('second', 59).valueOf()
    }
    const data = await ieoApi.subHistory(body)

    list = data.records
    total = data.total
  } finally {
    isRefresh = false
  }
}
console.log('ss', dayjs(1673452799).format('YYYY/MM/DD HH:mm'))
const onTimeConfirm = (key, e) => {
  const time = e.selectedValues
  // const time = dayjs(e.selectedValues.join('-')).set('hour', 0).set('minute', 0).set('second', 0).valueOf()
  if (key == 'startTime') {
    form.startTime = time
    showStartTime = false
  }
  if (key == 'endTime') {
    form.endTime = time
    showEndTime = false
  }
}

const onTimeCancel = (key) => {
  if (key == 'startTime') {
    showStartTime = false
  }
  if (key == 'endTime') {
    showEndTime = false
  }
}

const onClickLeft = () => {
  router.go(-1)
}

watch(() => isSearch, (show) => {
  if (show == true) {
    openRefresh = false
  } else {
    openRefresh = true
  }
})

winMes.postParent('hiddenNaviBar')

onBeforeMount(() => {
  getData()
  inModule(['/ieo', '/ieo/projectInfo'])
})

</script>
<style lang="scss">
.SubscriptionHistory-search{
  --el-component-size: 60px;
  .el-input__inner{
    font-size: 24px;
  }
  .SelectBox{
    .row{
      margin-right: 15.8px;
    }
    .row::nth-child(3){
      margin-right: 0px;
    }
  }
}
</style>
